* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}
.digital-clock {
  width: 400px;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
  box-shadow: 1px 1px 8px black;
}
h1 {
  color: aqua;
  font-weight: 600;
  font-size: 40px;
  margin-bottom: 15px;
}
.time {
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 50px;
}
.date {
  font-size: 20px;
  color: black;
}